import React, {Component} from 'react';
import ReactDOM from 'react-dom';

// three.js imports
import * as THREE from "three";
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { VertexNormalsHelper } from 'three/examples/jsm/helpers/VertexNormalsHelper';
import { VertexTangentsHelper } from 'three/examples//jsm/helpers/VertexTangentsHelper';
import { BufferGeometryUtils } from 'three/examples//jsm/utils/BufferGeometryUtils';

import './index.css';


// import Gallery from "react-photo-gallery";
// import Carousel, { Modal, ModalGateway } from "react-images";

import './style/style.css';


import { motion } from "framer-motion"

import * as W69 from './Windows69.jsx';
// import * as Content from './content.jsx';
import * as Pet from './Pet.jsx'



class Desktop extends Component{
  // ...
  render(){
    return(
      <W69.Icon
        image={require('./img/bonzi.png')}
        name="pet.exe"
        id="petIcon"
        className="desktopApp"

        windowId="PetWindow" 
        content={<Pet.Pet/>}
        title="virtual pet"
        />
      )
  }
}



class App extends Component{

  componentDidMount() {
  var scene, renderer;
  var camera, light;
  var vnh;
  var vth;


  

        renderer = new THREE.WebGLRenderer();
        renderer.setPixelRatio( window.devicePixelRatio );
        renderer.setSize( window.innerWidth, window.innerHeight );
        // document.body.appendChild( renderer.domElement );
        this.mount.appendChild(renderer.domElement);


        //

        camera = new THREE.PerspectiveCamera( 70, window.innerWidth / window.innerHeight, 1, 1000 );
        camera.position.z = 400;

        scene = new THREE.Scene();

        light = new THREE.PointLight();
        light.position.set( 200, 100, 150 );
        scene.add( light );

        scene.add( new THREE.PointLightHelper( light, 15 ) );

        var gridHelper = new THREE.GridHelper( 400, 40, 0x0000ff, 0x808080 );
        gridHelper.position.y = - 150;
        gridHelper.position.x = - 150;
        scene.add( gridHelper );

        var polarGridHelper = new THREE.PolarGridHelper( 200, 16, 8, 64, 0x0000ff, 0x808080 );
        polarGridHelper.position.y = - 150;
        polarGridHelper.position.x = 200;
        scene.add( polarGridHelper );

        var loader = new GLTFLoader();
        loader.load( 'three/examples/models/gltf/LeePerrySmith/LeePerrySmith.glb', function ( gltf ) {

          var mesh = gltf.scene.children[ 0 ];

          BufferGeometryUtils.computeTangents( mesh.geometry ); // generates bad data due to degenerate UVs

          var group = new THREE.Group();
          group.scale.multiplyScalar( 50 );
          scene.add( group );

          // To make sure that the matrixWorld is up to date for the boxhelpers
          group.updateMatrixWorld( true );

          group.add( mesh );

          vnh = new VertexNormalsHelper( mesh, 5 );
          scene.add( vnh );

          vth = new VertexTangentsHelper( mesh, 5 );
          scene.add( vth );

          scene.add( new THREE.BoxHelper( mesh ) );

          var wireframe = new THREE.WireframeGeometry( mesh.geometry );
          var line = new THREE.LineSegments( wireframe );
          line.material.depthTest = false;
          line.material.opacity = 0.25;
          line.material.transparent = true;
          line.position.x = 4;
          group.add( line );
          scene.add( new THREE.BoxHelper( line ) );

          var edges = new THREE.EdgesGeometry( mesh.geometry );
          var line = new THREE.LineSegments( edges );
          line.material.depthTest = false;
          line.material.opacity = 0.25;
          line.material.transparent = true;
          line.position.x = - 4;
          group.add( line );
          scene.add( new THREE.BoxHelper( line ) );

          scene.add( new THREE.BoxHelper( group ) );
          scene.add( new THREE.BoxHelper( scene ) );


        } );

        //

        window.addEventListener( 'resize', onWindowResize, false );


      function onWindowResize() {

        camera.aspect = window.innerWidth / window.innerHeight;
        camera.updateProjectionMatrix();

        renderer.setSize( window.innerWidth, window.innerHeight );

      }

      function animate() {

        requestAnimationFrame( animate );

        var time = - performance.now() * 0.0003;

        camera.position.x = 400 * Math.cos( time );
        camera.position.z = 400 * Math.sin( time );
        camera.lookAt( scene.position );

        light.position.x = Math.sin( time * 1.7 ) * 300;
        light.position.y = Math.cos( time * 1.5 ) * 400;
        light.position.z = Math.cos( time * 1.3 ) * 300;

        if ( vnh ) vnh.update();
        if ( vth ) vth.update();

        renderer.render( scene, camera );

      }

    animate(); 
    }


  render() {
    // const {isVisible} = this.state;
      {console.log(window.screen.width/window.screen.height)}
    return (
        <div className="content" id="main">
        <Desktop/>
        <div ref={ref => (this.mount = ref)} style={{display:'none'}}/>
        </div>
      )
  }
}

ReactDOM.render(<App/>, document.getElementById('root'));