import React, {Component} from 'react';
import ReactDOM from 'react-dom';
import speechBlip from './audio/185828__lloydevans09__little-thing.wav'
import * as face from './henFaces.jsx';
import {story} from './MasterStory.jsx';
import GlitchClip from 'react-glitch-effect/core/Clip';


class WorldState{
	constructor(x, convo_cond, food_cond, love_cond){
		this.x = x
	}
	sayOwie(){
		alert("owie"+this.x)
	}
}




class Pet extends Component{

	constructor(props){
	    super(props)
	    this.state = {hunger:5,
	    			  love:5,
					  text:"> Heya My name's Hen, the human pet! 💖",
					  conversations:0,
	    			  speaking:false,
	    			  emotion:"basic",
	    			  name:"unknown",
	    			  faith:0
	    			  }
	    this.feed.bind(this);
	    this.typeWriter.bind(this);
	    this.boop = new Audio(speechBlip);
	    console.log(face);
	  }



	feed(){
		if(!this.state.speaking){
		  	this.setState({speaking:true})
		  	if(this.state.hunger==4){
		  		this.setState({faith:this.state.faith+1});
		  	}
		  	this.setState({text:"", 
		  				   emotion:story.feed[this.state.hunger].emotion
		  				},
		  				e=>this.typeWriter(story.feed[this.state.hunger].text,0));
		  	}

			


	}
	
	pet(){
		if(!this.state.speaking){
		  	this.setState({speaking:true})
		  	if(this.state.love==14 || this.state.love==18){
		  			this.setState({faith:this.state.faith+1})
		  	}
		  	this.setState({text:"", 
		  				   emotion:story.pet[this.state.love].emotion
		  				},
		  				e=>this.typeWriter(story.pet[this.state.love].text,0));
			  	}

			}
	talk(){

		if(!this.state.speaking){
			if(this.state.faith>=2){
				this.setState({faith:0,conversations:100})
			}
			this.setState({speaking:true})
			switch(this.state.conversations){
				case 12:
					this.state.name=alert("what is your name?","");
					break;
 				case 13:
 					this.setState({hunger:2});
 					break;
 				case 18:
 					this.setState({conversations:17});
 					break;
 				default:
 					break;
			}
			this.setState({text:"", 
			  			   emotion:story.talk[this.state.conversations].emotion
			  			},
			  			e=>this.typeWriter(story.talk[this.state.conversations].text,0));
			}
				
	}

	typeWriter(txt,i,speed=50,callback=()=>{}) {
	  if(i==txt.length){
	  	this.setState({speaking:false})
	  	callback()
	  	return
	  }
	  else{
	  	  let ma =  new Audio(speechBlip);
	  	  ma.play();
		  this.setState({text:this.state.text+txt.charAt(i)})
		  setTimeout(()=>this.typeWriter(txt,i+1,speed),speed);

	  }

	}

	render(){
		return(
	<div className="">
		<div class="petText">{this.state.text}</div>
		<div class="petDisplay"><center>
			<img src={face.emotions[this.state.emotion]}/>
		</center></div>

		<div className="petControls">
			<div className="petUIbutton"
				onClick={()=>this.setState({hunger:this.state.hunger+1}, e=>this.feed())}>
				feed</div> 
			<div className="petUIbutton"
				onClick={()=>this.setState({love:this.state.love+1}, e=>this.pet())}>
				pet</div>
			<div className="petUIbutton"
						onClick={() => this.setState({ conversations: this.state.conversations + 1 }, e=>this.talk())}>
				talk</div>
		</div>
	</div>

			)

		}
}

export {Pet}